import React from "react";
import {Calendar, momentLocalizer, Views} from 'react-big-calendar';
import moment from 'moment';
import 'react-big-calendar/lib/css/react-big-calendar.css';


export default function CalendarComponent() {

    let allViews = [Views.MONTH, Views.WEEK];
    // let allViews = Object.keys(Views).map(k => Views[k])
    let localizer = momentLocalizer(moment);
    let events = [
        {
            id: 0,
            title: 'Scheduled Work',
            allDay: true,
            start: moment("2020-06-08"),
            end: moment("2020-06-08"),
        },
        {
            id: 1,
            title: 'Scheduled Work',
            start: moment("2020-06-09"),
            end: moment("2020-06-09"),
        },
        {
            id: 1,
            title: 'Scheduled Work',
            start: moment("2020-06-10"),
            end: moment("2020-06-10"),
        },
        {
            id: 1,
            title: 'Off Day',
            start: moment("2020-06-11"),
            end: moment("2020-06-11"),
        },
        {
            id: 1,
            title: 'Off Day',
            start: moment("2020-06-12"),
            end: moment("2020-06-12"),
        }
    ]

    const ColoredDateCellWrapper = ({ children }) =>
        React.cloneElement(React.Children.only(children), {
            style: {
                backgroundColor: 'lightblue',
            },
        })

    return <div style={{'height': 400}}>
        <Calendar
            events={events}
            views={allViews}
            step={60}
            showMultiDayTimes
            defaultDate={new Date()}
            components={{
                timeSlotWrapper: ColoredDateCellWrapper
            }}
            localizer={localizer}
        />
    </div>
}